$main-color:#1e89f1ab;
$black:#000;
$white:#fff;
$primary:#0e569dab;
// var
%flex {
    display: flex;
    align-items: center;
}

%center {
    position: absolute;
    left: 50%;
    top: 50%;
}

%trans {
    transition: all .5s ease-in-out;
}

%dimen {
    width: 100%;
    height: 100%;
}

input,
input:focus,
.close-search,
button,
button:focus,
a:link,
.form-control:focus {
    text-decoration: none;
    outline: none;
    border: none;
    box-shadow: none;
}

// footer
footer {
    span {
        display: none;
        position: relative;
        left: 0;
        top: 0;
        font-size: 18px;
        cursor: pointer;
        font-size: 14px;
        transition: all .5s ease-in-out;
        &::after {
            position: relative;
            left: 0;
            top: 0;
            content: "\f067";
            font-family: "Font Awesome 5 free";
            font-weight: 900;
            color: #fff;
            transition: all .5s ease-in-out;
        }
        @media(max-width:767px) {
            & {
                display: block;
            }
        }
    }
}