        body,
        html {
            overflow-x: hidden;
        }
        
        .header {
            height: 100vh;
            .nav-bar {
                padding: 25px 0px;
                position: fixed;
                z-index: 4;
                width: 100%;
                overflow: hidden;
                &.add {
                    background-color: $black;
                }
                .contain {
                    @extend %flex;
                    justify-content: space-between;
                    position: relative;
                    .closed {
                        display: none;
                        @include Sm {
                            display: block;
                        }
                    }
                    .logo img {
                        width: 250px;
                    }
                    .slide-bar {
                        display: flex;
                        ul {
                            @extend %flex;
                            justify-content: center;
                            margin-bottom: 0;
                            margin-top: 30px;
                            li {
                                margin-left: 40px;
                                @include Sm {
                                    margin-left: 0;
                                }
                                a {
                                    font-family: 'Roboto-Regular';
                                    font-size: 16px;
                                    color: $white;
                                    text-transform: uppercase;
                                    position: relative;
                                    display: inline-block;
                                    overflow: hidden;
                                    &:before {
                                        content: '';
                                        position: absolute;
                                        bottom: 0;
                                        left: 100%;
                                        background-color: $main-color;
                                        width: 100%;
                                        height: 2px;
                                        @extend %trans;
                                    }
                                    &:hover::before {
                                        left: 0%;
                                    }
                                }
                                @include Sm {
                                    &#search {
                                        display: none;
                                    }
                                }
                            }
                        }
                        .search {
                            position: fixed;
                            background-color: rgba(0, 0, 0, 0.7);
                            @extend %dimen;
                            z-index: 99999;
                            left: 0;
                            top: 0;
                            @extend %flex;
                            justify-content: center;
                            opacity: 0;
                            visibility: hidden;
                            @extend %trans;
                            &.active {
                                opacity: 1;
                                visibility: visible;
                            }
                            .input {
                                display: flex;
                                @include mob375 {
                                    width: 300px;
                                }
                                input {
                                    width: 400px;
                                    height: 40px;
                                    padding-left: 20px;
                                    background-color: $white;
                                    &::placeholder {
                                        font-size: 20px;
                                    }
                                    &:focus::placeholder {
                                        color: #ccc;
                                    }
                                }
                            }
                            .close-search {
                                position: absolute;
                                top: 30%;
                                border-radius: 50%;
                                width: 40px;
                                height: 40px;
                                @extend %flex;
                                justify-content: center;
                                left: 50%;
                                transform: translate(-50%, -30%);
                                cursor: pointer;
                                background-color: $main-color;
                                i {
                                    color: $white;
                                }
                            }
                            button {
                                width: 80px;
                                height: 40px;
                                background-color: $primary;
                                color: $white;
                                cursor: pointer;
                            }
                        }
                    }
                }
                .toggle {
                    position: relative;
                    display: none;
                    margin-top: 10px;
                    span {
                        position: absolute;
                        width: 30px;
                        height: 3px;
                        background-color: $white;
                        &::after,
                        &:before {
                            content: '';
                            position: absolute;
                            @extend %dimen;
                            background-color: $white;
                        }
                        &::after {
                            top: 10px;
                        }
                        &::before {
                            top: -10px;
                        }
                    }
                }
                @media(max-width:991px) {
                    .toggle {
                        display: block;
                        cursor: pointer;
                        position: absolute;
                        right: 30px;
                    }
                    .slide-bar {
                        position: fixed;
                        top: 0;
                        right: 0;
                        width: 100%;
                        height: 100%;
                        background-color: $white;
                        z-index: 9999;
                        transform-origin: right;
                        z-index: 999999;
                        transform: scaleX(0);
                        transition: all .5s ease-in-out;
                        &.active {
                            transform: scaleX(1);
                        }
                        &.transform-origin {
                            transform-origin: left;
                        }
                        .closed {
                            position: absolute;
                            top: 0;
                            width: 100%;
                            padding: 0px 30px;
                            height: 80px;
                            display: flex;
                            align-items: center;
                            justify-content: space-between;
                            cursor: pointer;
                            i {
                                display: block!important;
                            }
                        }
                        nav {
                            width: 100%;
                            ul {
                                display: block!important;
                                width: 300px;
                                margin: 100px auto 0px !important;
                                text-align: center;
                                li {
                                    padding: 20px 0px;
                                    a {
                                        font-weight: bold;
                                        color: #333!important;
                                    }
                                }
                            }
                        }
                    }
                }
            }
            .overlay {
                background-image: url(../images/layer-2.png);
                background-size: cover;
                height: 100%;
                clip-path: polygon(0% 0, 100% 0, 100% 71%, 75% 100%, 0 80%);
                position: relative;
                ul.mediaa {
                    position: absolute;
                    top: 50%;
                    left: 0;
                    z-index: 2;
                    transform: translateY(-50%);
                    li {
                        background-color: $main-color;
                        a {
                            width: 52px;
                            height: 52px;
                            line-height: 52px;
                            text-align: center;
                            background-color: $primary;
                            border: 1px solid #ffffff26;
                            display: inline-block;
                            color: $white;
                            font-size: 20px;
                            @extend %trans;
                        }
                        &:hover a {
                            transform: translateX(25px);
                        }
                    }
                }
                .swiper-container {
                    @extend %dimen;
                    .swiper-slide {
                        text-align: center;
                        @extend %flex;
                        justify-content: center;
                        background-image: url(../images/layer-2.png);
                        background-size: cover;
                        .over {
                            position: absolute;
                            left: 0;
                            top: 0;
                            @extend %dimen;
                            background: rgba(0, 0, 0, .7);
                            color: #fff;
                            .center {
                                @extend %center;
                                transform: translate(-50%, -50%);
                                width: 35%;
                            }
                            h1 {
                                font-family: 'Roboto-Medium';
                            }
                            p {
                                font-family: 'Segoe-semilight';
                                font-size: 22px;
                            }
                            button {
                                @extend %trans;
                                text-transform: uppercase;
                                background-color: $main-color;
                                padding: 8px 25px;
                                color: $white;
                                margin-top: 20px;
                                a {
                                    color: $white;
                                    margin-right: 8px;
                                    font-family: 'Roboto-Regular';
                                }
                                &:hover {
                                    background-color: transparent;
                                    border: 2px solid $main-color;
                                }
                            }
                        }
                    }
                }
                .swiper-pagination-fraction {
                    left: 75%;
                    transform: translateX(-75%);
                    color: $white;
                    z-index: 1;
                    width: auto;
                }
            }
            @media(max-width:991px) {
                .overlay {
                    clip-path: none!important;
                }
                .center {
                    width: 50%!important;
                }
                .swiper-pagination-fraction {
                    left: 50%!important;
                    transform: translateX(-50%)!important;
                    bottom: 30px!important;
                }
            }
            @media(max-width:767px) {
                .center {
                    top: 70%!important;
                    transform: translate(-50%, -70%)!important;
                }
            }
            @media(max-width: 567px) {
                .mediaa {
                    display: none!important;
                }
                .center {
                    width: 80%!important;
                    top: 60%!important;
                    transform: translate(-50%, -60%)!important;
                    h1 {
                        font-size: 25px!important;
                    }
                    p {
                        font-size: 18px!important;
                    }
                    button {
                        font-size: 15px!important;
                        padding: 8px 15px!important;
                        margin-top: 10px!important;
                    }
                }
            }
            .input input {
                width: 300px!important;
            }
        }
        
        // about-us
        .about-us {
            padding: 100px 0px 30px;
            overflow: hidden;
            img {
                @extend %dimen;
            }
            h2 {
                color: #000;
                text-transform: uppercase;
                font-size: 40px;
                letter-spacing: 2px;
                margin-bottom: 30px;
                position: relative;
                font-family: 'Roboto-Medium';
                &::after {
                    position: absolute;
                    left: 0;
                    top: -25px;
                    content: 'who we are';
                    width: 100%;
                    font-size: 80px;
                    color: #EDE7E3;
                    z-index: -1;
                }
            }
            .first {
                text-align: left;
                img {
                    display: none;
                }
                p {
                    color: #606164;
                    margin-bottom: 0;
                    font-size: 20px;
                    margin-bottom: 0;
                    font-family: 'Roboto-Regular';
                }
                button {
                    @extend %trans;
                    text-transform: uppercase;
                    border: 2px solid $main-color;
                    padding: 8px 25px;
                    color: #000;
                    background: $white;
                    margin-top: 50px;
                    position: relative;
                    display: inline-block;
                    overflow: hidden;
                    font-family: 'Roboto-Regular';
                    &::after {
                        content: '';
                        position: absolute;
                        right: 100%;
                        top: 0;
                        @extend %dimen;
                        background-color: $main-color;
                        @extend %trans;
                    }
                    &:hover::after {
                        right: 0;
                        color: $white;
                    }
                    a {
                        color: $black;
                        margin-right: 10px;
                    }
                    &:hover a,
                    &:hover i {
                        color: $white;
                        z-index: 1;
                        position: relative;
                    }
                }
            }
            @media(max-width:991px) {
                & {
                    padding: 50px 0px;
                    .last {
                        display: none;
                    }
                    .first {
                        flex: 0 0 100%;
                        max-width: 100%;
                        text-align: center;
                        img {
                            display: block;
                            width: auto;
                            height: auto;
                            margin: auto;
                            margin-bottom: 30px;
                        }
                        button {
                            margin-top: 20px;
                        }
                    }
                }
            }
            @media(max-width:576px) {
                .first {
                    img {
                        width: 100%!important;
                        margin-bottom: 20px;
                    }
                    p {
                        font-size: 15px!important;
                    }
                    button {
                        padding: 5px 15px!important;
                    }
                }
            }
        }
        
        // about us
        // PRODUCTS
        .PRODUCTS {
            padding: 50px 0px;
            overflow: hidden;
            h2 {
                color: #000;
                text-transform: uppercase;
                font-size: 45px;
                letter-spacing: 2px;
                margin-bottom: 30px;
                position: relative;
                font-family: 'Roboto-Medium';
                &::after {
                    position: absolute;
                    left: 0;
                    top: -25px;
                    content: 'PRODUCTS';
                    width: 100%;
                    font-size: 80px;
                    color: #EDE7E3;
                    z-index: -1;
                }
            }
            p {
                font-size: 18px;
                width: 70%;
                margin: auto;
                text-transform: capitalize;
                text-align: center;
                font-family: 'Roboto-Regular';
            }
            .row {
                margin-top: 50px;
                .col-lg-4 {
                    overflow: hidden;
                    .all,
                    .images,
                    .image {
                        height: 100%;
                        img {
                            @extend %dimen;
                        }
                    }
                }
                .col-lg-8 {
                    padding: 0;
                    .thumb {
                        position: relative;
                        text-align: center;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        width: 100%;
                        .all {
                            width: 50%;
                            height: 250px;
                            padding: 10px;
                            overflow: hidden;
                            img {
                                height: 100%;
                                width: 100%;
                                object-fit: cover;
                            }
                            &.one {
                                width: inherit;
                                max-width: 30%;
                                flex: 0 0 30%;
                            }
                            &.two {
                                width: inherit;
                                max-width: 70%;
                                flex: 0 0 70%;
                            }
                        }
                    }
                }
                .image {
                    @extend %dimen;
                    object-fit: cover;
                    overflow: hidden;
                    position: relative;
                    .hover-effect {
                        position: absolute;
                        left: -100%;
                        top: 0;
                        @extend %dimen;
                        background-color: #001439;
                        opacity: .8;
                        @extend %flex;
                        justify-content: center;
                        @extend %trans;
                        .hover {
                            @extend %center;
                            transform: translate(-50%, -50%);
                            @extend %trans;
                            transition-delay: .3s;
                            left: -100px;
                            span {
                                color: $white;
                                text-transform: capitalize;
                                font-size: 18px;
                                display: block;
                            }
                            button {
                                position: relative;
                                display: inline-block;
                                overflow: hidden;
                                background: transparent;
                                padding: 5px 10px;
                                border: 2px solid #1e89f1ab;
                                margin-top: 20px;
                                color: $white;
                                width: 130px;
                                a {
                                    color: $white;
                                    text-transform: uppercase;
                                }
                            }
                        }
                    }
                    &:hover .hover-effect {
                        left: 0;
                    }
                    &:hover .hover {
                        left: 50%;
                    }
                }
            }
            @media(max-width:991px) {
                p {
                    width: 80%!important;
                }
                .col-lg-4 .image {
                    height: 250px !important;
                }
            }
            @media(max-width: 767px) {
                .all.one,
                .all.two {
                    max-width: 50%!important;
                    flex: 0 0 50%!important;
                }
            }
            @media(max-width:576px) {
                & {
                    padding: 20px 0px;
                    h2 {
                        font-size: 30px!important;
                        margin-bottom: 20px!important;
                        &::after {
                            font-size: 60px!important;
                        }
                    }
                    p {
                        font-size: 15px!important;
                        width: 100%!important;
                        padding: 0px 15px;
                    }
                    .images {
                        width: 80%!important;
                        margin: auto!important;
                    }
                    .thumb {
                        flex-direction: column;
                        padding: 10px;
                        .all {
                            width: 80%!important;
                        }
                        .all.one,
                        .all.two {
                            max-width: 80%!important;
                            flex: 0 0 80%!important;
                        }
                    }
                }
            }
        }
        
