                        color: $primary;
                    }
                }
                .owl-dots {
                    display: none;
                    @include Sm {
                        display: block;
                        margin-top: 30px;
                    }
                    button.owl-dot {
                        width: 12px;
                        height: 12px;
                        border-radius: 50%;
                        margin-left: 10px;
